.photo-list {
  display: grid;
  grid-template-columns: repeat(3,minmax(223px,1fr));
  grid-gap: 16px 16px;
  justify-content: center;
  margin: 30px auto;
  list-style: none; 
}

@media (max-width: 900px) {
  .photo-list {
    grid-template-columns: repeat(auto-fit,minmax(223px,1fr));
  }
}